
























































































































































































































































































body {
    background-color: #f8f8f8;
}

.g-page {
    padding: 35px;
    .g-page__header {
        margin-bottom: 10px;
    }
    .g-page__footer {
        padding-top: 20px;
        .g-page__footerSubmit {
            padding-top: 40px;
            text-align: center;
            .u-btn-genarate {
                padding: 0 35px;
                font-size: 16px;
                line-height: 2;
            }
        }
        .g-page__generate {
            margin-top: 40px;
        }
    }
}

.m-header {
    display: flex;
    flex-direction: row;
    /*user-select: none;*/
    .m-header__logo {
        clear: both;
        line-height: 30px;
        color: #444;
        .m-header__logoSide{
            float: left;
        }
        .m-header__logoMain{
            overflow: hidden;
        }
        .txt {
            font-size: 26px;
            line-height: 55px;
            vertical-align: middle;
        }
        .version {
            font-size: 14px;
            vertical-align: middle;
        }
        .u-icon-logo-horse {
            font-size: 50px;
            line-height: 1;
            color: #444;
            vertical-align: middle;
            margin-right: 14px;
        }
    }
    .m-header__show {
        flex: 1;
        text-align: right;
        font-size: 14px;
        line-height: 30px;
        color: #666;
        .m-header__zoom {
            display: inline-block;
            line-height: 30px;
            text-align: left;
            vertical-align: middle;
            .u-icon-zoomin, .u-icon-zoomout {
                font-size: 20px;
                line-height: 30px;
            }
        }
        .m-header__rotate {
            position: relative;
            display: inline-block;
            line-height: 50px;
            text-align: left;
            margin-left: 30px;
            padding-right: 30px;
            vertical-align: middle;
            .u-icon-document, .u-icon-document-h {
                position: absolute;
                right: 10px;
                top: 50%;
                transform: translateY(-50%) rotate(0deg);
                font-size: 24px;
                line-height: 30px;
                &.z-horizontal {
                    transform: translateY(-50%) rotate(90deg);
                }
            }
        }
        .number {
            margin-left: 3px;
            margin-right: 3px;
        }
    }
}

.m-canvas {
    box-sizing: border-box;
    /*overflow: hidden;*/
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 30px;
    outline: 1px dashed #ccc;
    background-color: #fff;
    &:hover {
        .u-corner-page {
            visibility: visible;
            opacity: 1;
            transition: 0.5s;
        }
    }
    .m-canvas__title {
        font-size: 30px;
        line-height: 1.5;
        text-align: left;
        padding-bottom: 30px;
        .ttl {
            font-size: 30px;
            line-height: 1.5;
            height: 45px;
            outline: 1px dashed #ccc;
        }
    }
    .m-canvas__area {
        position: relative;
        display: flex;
        flex-direction: column;
        flex: 1;
        &.m-canvas__material {
            .u-corner{
                transform:translateY(-100%);
            }
            &:hover {
                .u-corner-material {
                    visibility: visible;
                    opacity: 1;
                    transition: 0.5s;
                }
            }
        }
    }
    .u-corner {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        left: 0;
        top: 0;
        padding: 0 10px;
        background-color: rgba(200, 200, 100, 0.2);
        font-size: 12px;
        line-height: 1.6;
        color: #666;
    }
}

.m-imageList {
    /*overflow: hidden;*/
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    flex: 1;
    outline: 1px dashed #ccc;
    &.z-col3{
        li{
            width: 33.3%;
        }
    }
    &.z-row5{
        li{
            flex-basis: 20%;
        }
    }
    li {
        overflow: hidden;
        box-sizing: border-box;
        width: 50%;
        flex: 0 0 25%;
        border: 1px solid #ccc;
        .m-imageList__cell{
            box-sizing: border-box;
            display: table;
            table-layout: fixed;
            position: relative;
            height: 100%;
            font-size: 0;
            line-height: 0;
            &:hover{
                .u-icon-closeb{
                    visibility: visible;
                    opacity: 1;
                    transition: 0.5s;
                }
            }
            .m-imageList__cellSerial{
                box-sizing: border-box;
                display: table-cell;
                border-right: 1px solid #ddd;
                vertical-align: middle;
                font-size: 14px;
                text-align: center;
            }
            .m-imageList__cellImg{
                box-sizing: border-box;
                display: table-cell;
                /*width: 25%;*/
                border-right: 1px solid #ddd;
                vertical-align: middle;
            }
            .m-imageList__cellPos{
                box-sizing: border-box;
                display: table-cell;
                /*width:20%;*/
                padding: 10px;
                border-right:1px solid #ddd;
                vertical-align: middle;
                white-space: normal;
                font-size: 14px;
                line-height: 1.5;
                color: #333;
                outline: 0;
                &:focus{
                    background-color: aliceblue;
                }
            }
            .m-imageList__cellNote{
                box-sizing: border-box;
                display: table-cell;
                /*width: 55%;*/
                padding: 10px;
                vertical-align: middle;
                white-space: normal;
                word-break: break-all;
                font-size: 14px;
                line-height: 1.5;
                color: #333;
                outline: 0;
                &:focus{
                    background-color: aliceblue;
                }
            }
        }
        .img {
            max-width: 100%;
            max-height: 100%;
        }
        .txt {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            transform: translateY(100%);
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            text-align: center;
            font-size: 14px;
            line-height: 2;
            color: #333;
            text-shadow: 1px 1px 0 #fff;
        }
        .u-icon-closeb {
            visibility: hidden;
            opacity: 0;
            position: absolute;
            left: 0;
            top: 0;
            transform: translate(0%, 0%);
            width: 20px;
            height: 20px;
            background-color: rgba(200, 200, 100, 0.5);
            font-size: 14px;
            color: #333;
            line-height: 20px;
            text-align: center;
            border-radius: 50%;
            cursor: pointer;
        }
    }
}
.m-titleList{
    display: table;
    table-layout: fixed;
    width: 100%;
    font-size: 0;
    line-height: 0;
    li{
        box-sizing: border-box;
        display: table-cell;
        /*padding: 5px;*/
        border: 1px solid #ccc;
        border-right-width: 0;
        white-space: nowrap;
        .item{
            overflow: hidden;
            box-sizing: border-box;
            display: inline-block;
            font-size: 14px;
            line-height: 1.5;
            color: #333;
            text-align: center;
            text-overflow: ellipsis;
            white-space: nowrap;
            border-right: 1px solid #ddd;
        }
    }
}

.m-setting {
    margin-top: 30px;
    .m-setting__header {
    }
